.landingPage{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-size: cover;
    background-position: center center;
    width: 100vw;
    height: 98vh;
    box-sizing: border-box;
}

.landingPage h1 {
    margin-top: -30px;
    margin-bottom: 30px;
    color: aliceblue;
    font-size: 2vw;
}

.landingPage button {
    color: #e1e8eb;
    background: rgba(0, 0, 0, .3);
    width: 140px;
    height: 50px;
    font-size: x-large;
    border-radius: 5px;
    cursor: pointer;
}

video{
    position: fixed;
    z-index: -1;
    box-sizing: border-box;
}